<template>
  <div id="app">
    <div class="error_img">
      <div class="error_type">
        错误类型 - <span style="font-weight:900">403</span>
      </div>
      <br>
      <div class="error_tiding">
        <span class="sorry">对不起！你没有访问该页面的权限</span><img class="sorryimg" src="../../static/img/weiqu.png" alt="">
      </div>
      <div class="tologin">
        <span>
          {{ time }}
          </span>  秒后跳转到登录界面
      </div>
    </div>
  </div>
</template>

<script>
import { setTimeout, clearInterval } from 'timers';
export default {
  data() {
    return {
      time:3
    }
  },
  mounted(){

    this.tiaozhuan();
    
  },
  methods:{
    tiaozhuan(){
      var that = this;
      setInterval(function(){
        that.time --;
      },1000)
      setTimeout(function(){
        // that.$router.push({
        //   path: '/login'
        // })
        location.href = that.baseUrl01   
      },3000);
    }

  },
}
</script>

<style>
.error_img{
  width: 600px;
  height: 400px;
  margin: 100px auto;
  text-align: center;
}
.error_type{
  font-size: 30px;
}
.error_tiding{
  font-size: 25px;
  height: 210px;
  position: relative;
  /* overflow: hidden; */
  /* line-height: 210px; */
  /* width: 200px; */
}
.sorry{
  position: absolute;
  left: 0;
  display: inline-block;
  height: 210px;
  line-height: 210px;
}
.sorryimg{
  float: right;
}

</style>




